import productList from '@/components/productList.vue'
import interfaces from '@/utils/interfaces.js'
export default {
	components: {
		productList
	},
	data() {
		return {
			filterIndex: '',
			defalutSort: 1, //是否综合排序 0 否 1 是 如果为 1
			priceSort: "", //价格排序 asc 升序 desc 降序
			timeSort: "", //剩余时间排序 asc 升序 desc 降序
			pageSize: 10, //分页大小
			pageNum: 1, //页码
			type: "", //类型1 发布采购 2 发布销售 3 委托销售
			loadingText: "正在加载....",
			cate_id: "",
			firstImages: "",
			goodsList: [],
			kind_id: "",
		}
	},
	onLoad(option) {
		this.kind_id = option.id;
		uni.setNavigationBarTitle({
			title: option.name
		});
		this.loadData();
	},
	methods: {
		loadData() {
			let params = {
				data: {
					page_size: this.pageSize,
					page_index: this.pageNum,
					keyword: "",
					type:'',
					price_sort: this.priceSort,
					remain_time_sort: this.timeSort,
					cate_id: '',
					brand_id: '',
					is_defalut_sort: this.defalutsort,
					kind_id: this.kind_id,
				}
			}
			console.log(params);
			this.request({ //分类产品列表
				url: interfaces.getNeedsData,
				dataType: "JSON",
				method: 'POST', //请求方式
				data: params,
				success: (res) => {
					if (res.code == 200) {
						
						var lists = res.data.list;
						for(let i=0;i<lists.length;i++){//转数组
							let serviceData =lists[i].service_cnname !=null && lists[i].service_cnname.length?lists[i].service_cnname.split(','):'';
							lists[i].service_cnname = serviceData;
							let qualification = lists[i].qualification_icon !=null && lists[i].qualification_icon.length?lists[i].qualification_icon.split(','):'';
							lists[i].qualification_icon = qualification;
						}	
						if(lists.length<this.pageSize){
							this.loadingText = "到底了";
						}
						if (lists.length > 0) {
							lists.forEach(item => {
								this.goodsList.push(item);
							})
						} else {
							this.loadingText = "到底了";
						}	
					}
				}
			})
		},
		handleSelect(index) {
			this.filterIndex = index;
			if (index === 0) {
				this.defalutSort = this.defalutSort === 1 ? 0 : 1;
			} else {
				this.defalutSort = '';
			}
			if (index === 1) {
				switch (this.priceSort) {
					case 'asc':
						this.priceSort = 'desc';
						break;
					case 'desc':
						this.priceSort = 'asc';
						break;
					default:
						this.priceSort = 'desc';
						break;
				}
			}
			if (index === 2) {
				switch (this.timeSort) {
					case 'asc':
						this.timeSort = 'desc';
						break;
					case 'desc':
						this.timeSort = 'asc';
						break;
					default:
						this.timeSort = 'desc';
						break;
				}
			}
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
		},
		updateValue(item) { //分类筛选
			this.cate_id = item.id;
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			this.closeDrawer('right');
		},

	},
	onPullDownRefresh() {
		setTimeout(() => {
			this.pageNum = 1;
			this.loadingText = "加载中...";
			this.goodsList = [];
			this.loadData();
			uni.stopPullDownRefresh();
		}, 1000)
	},
	// 上拉加载
	onReachBottom() {
		//debugger
		this.pageNum++;
		this.loadData();
	}
}
